	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/"],"configurations":["/configurations/overview/","/configurations/details/","/configurations/custom-webpack-config/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/configurations/overview/"},"frontmatter":{"title":"Configuration overview","id":"overview"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Detailed configuration overview","id":"details"}}}]},"markdownRemark":{"html":"<p>The default configuration file Wingsuit uses internally.</p>\n<h2 id=\"overview\"><a href=\"#overview\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Overview:</h2>\n<pre><code class=\"language-js\">export const wingsuit = {\n  designSystems: {\n    default: {\n      path: \"source/default\",\n      patternPath: \"patterns\",\n      namespaces: {}\n    }\n  },\n  environments: {\n    development: {},\n    production: {}\n  },\n  apps: {\n    storybook: {\n      type: \"storybook\",\n      path: \"./apps/storybook\",\n      cssMode: \"hot\",\n      distFolder: \"dist/app-storybook\",\n      assetBundleFolder: \"assets\",\n      designSystem: \"default\",\n      webpackBundles: [\n        \"DefaultBundle\",\n        \"StorybookAssetBundle\",\n        \"TwingBundle\",\n        \"StorybookBundle\",\n        \"CssBundle\",\n        \"BabelBundle\"\n      ]\n    },\n    drupal: {\n      type: \"drupal\",\n      path: \"./apps/drupal\",\n      cssMode: \"extract\",\n      distFolder: \"dist/app-drupal\",\n      assetBundleFolder: \"assets\",\n      designSystem: \"default\",\n      webpackBundles: [\n        \"BabelBundle\",\n        \"DefaultBundle\",\n        \"AssetBundle\",\n        \"DrupalBundle\",\n        \"CssBundle\"\n      ]\n    }\n  }\n}\n</code></pre>\n<h2 id=\"design-systems\"><a href=\"#design-systems\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Design systems</h2>\n<p>The <code>designSystems</code> section is where you define the folder structure of your design system.\nEach design system uses <code>namespaces</code> to configure folder aliases.</p>\n<h3 id=\"sample-namespaces\"><a href=\"#sample-namespaces\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Sample namespaces:</h3>\n<pre><code class=\"language-js\">const path = require('path');\n\nconst patterns = path.resolve(__dirname, 'patterns');\n\nmodule.exports = {\n  tokens: path.resolve(__dirname, 'tokens'),\n  protons: path.resolve(patterns, '00-protons'),\n  atoms: path.resolve(patterns, '01-atoms'),\n  molecules: path.resolve(patterns, '02-molecules'),\n  organisms: path.resolve(patterns, '03-organisms'),\n  templates: path.resolve(patterns, '04-templates'),\n  pages: path.resolve(patterns, '05-pages'),\n};\n</code></pre>\n<h3 id=\"namespaces\"><a href=\"#namespaces\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Namespaces:</h3>\n<p>With namespaces you can include twig components and javascript components.</p>\n<p><b>TWIG:</b></p>\n<pre><code class=\"language-js\">  {% include \"@atoms/button/button.twig\" %}\n</code></pre>\n<p><b>Javascript:</b></p>\n<pre><code class=\"language-js\">  import \"atoms/button\";\n</code></pre>\n<h2 id=\"environments\"><a href=\"#environments\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Environments</h2>\n<p>Under each environment you can overwrite default variables.</p>\n<p>Here a example to overwrite the cssMode for development:</p>\n<pre><code class=\"language-js\">    environments: {\n      ...\n          development: {\n            apps: {\n              storybook: {\n                cssMode: \"extract\"\n              }\n            }\n          }\n</code></pre>\n<h2 id=\"apps\"><a href=\"#apps\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Apps</h2>\n<p>Each apps has the following configuration keys:</p>\n<p><code>type</code>: The type of the app.</p>\n<p><code>path</code>: The path to the app.</p>\n<p><code>cssMode</code>: Extract css or not.</p>\n<p><code>distFolder</code>: The path to the dist folder.</p>\n<p><code>assetBundleFolder</code>: The folder name under the dist folder for assets.</p>\n<p><code>designSystem</code>: Link to the design system.</p>\n<p><code>webpackBundles</code>: The used webpack bundles. <a href=\"../custom-webpack-config\">Here you can find more informations</a> about webpack bundles.</p>","fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Detailed configuration overview","id":"details"}}},"pathContext":{"slug":"/configurations/details/"}}

